
.header {
  font-size: 18px;
  line-height: 24px;
  background: #f3f7f9;
}
.header-top {
  background-color: #293449;
  min-height: 42px;
}
.main-header {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  color: white;
  width: 960px;
  margin: 0 auto;
  padding: 0 0px 0 10px;
}
.header-logo {
  box-shadow: inset 0 -5px 0 rgba(0, 0, 0, 0.24);
  height: 47px;
  width: 220px;
  padding-top: 13px;
  margin-bottom: -5px;
  text-align: center;
  background-color: #ee3643;
}
.scan {
  position: relative;
  width: 270px;
  height: 42px;
  color: white;
}
.scan:hover {
  background-color: #212a3a;
}
.scan:hover .scan-icon::before {
  opacity: 1;
}
.scan-icon::before {
  content: "";
  position: absolute;
  height: 17px;
  top: 12px;
  left: 17px;
  width: 17px;
  opacity: 0.3;
  background: url("/src/img/icon-search.png") no-repeat;
}
.scan-input {
  padding-top: 12px;
  padding-bottom: 12px;
  font-size: 16px;
  padding-left: 45px;
  width: 270px;
  background: transparent;
  border: none;
  color: white;
}
input[type="search"]::-webkit-input-placeholder {
  color: white;
}
input[type="search"]::-moz-placeholder {
  color: white;
}
.order {
  background: #63a63e;
}
.header-item {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  padding: 0;
  margin: 0;
}
.header-item li {
  height: 42px;
  padding-top: 8px;
}
.nav-button {
  padding-left: 50px;
  padding-right: 22px;
  font-size: 17px;
  font-weight: 400;
  position: relative;
}
.nav-button1::before {
  content: "";
  top: 14px;
  left: 24px;
  width: 14px;
  height: 16px;
  position: absolute;
  opacity: 0.3;
  background: url("/src/img/icon-bookmark.svg");
}
.nav-button2::before {
  content: "";
  top: 14px;
  left: 24px;
  width: 15px;
  height: 15px;
  position: absolute;
  opacity: 0.3;
  background: url("/src/img/icon-cart.svg");
}
.bookmark:hover {
  background-color: #212a3a;
}
.bookmark:hover .nav-button1::before {
  opacity: 1;
}
.basket:hover {
  background-color: #212a3a;
}
.basket:hover .nav-button2::before {
  opacity: 1;
}
.order:hover {
  background-color: #5fbb2d;
}
.nav-button1,
.nav-button2 {
  padding-top: 13px;
  padding-bottom: 13px;
}
.nav-button__green {
  padding: 12px 30px;
}
.header-info {
  display: flex;
  justify-content: space-between;
  width: 960px;
  padding: 0 5px 0 35px;
  margin: 46px auto 0px;
}
.info__text {
  width: 200px;
  color: #ee3643;
  font-style: italic;
  margin: 0;
  line-height: 23px;
}
.info__telephone {
  position: relative;
}
.telephone__number {
  border: 3px solid white;
  color: #ee3643;
  text-align: center;
  padding: 8px 54px 8px 74px;
  font-size: 21px;
  line-height: 21px;
  margin: 0;
  margin-bottom: 2px;
}
.telephone__number::before {
  content: "";
  width: 19px;
  height: 19px;
  position: absolute;
  background: url("/src/img/icon-phone.png");
  left: 15px;
}
.info__adress {
  margin: 0 auto;
}
.info__login {
  position: relative;
}
.login-btn {
  background: white;
  border: none;
  width: 120px;
  height: 45px;
  padding-left: 20px;
}
.login-btn::before {
  content: "";
  position: absolute;
  width: 20px;
  height: 17px;
  opacity: 0.3;
  background: url("/src/img/icon-login.svg");
  left: 14px;
}
.login-btn:hover {
  color: #ee3643;
}
.login-btn:hover::before {
  opacity: 1;
}
.register-btn:hover {
  color: #ee3643;
}
.register-btn {
  background: white;
  border: none;
  width: 120px;
  height: 45px;
}
.header-navigation {
  margin-top: 44px;
  margin-bottom: 60px;
}
.main-navigation {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  max-width: 960px;
  flex-wrap: wrap;
  margin: 0 auto;
  padding: 0 20px;
  background-color: #32425c;
  min-height: 60px;
  box-shadow: inset 0 -5px 0 #293449;
}
.main-navigation li {
  text-align: center;
  font-size: 17px;
}
.main-navigation li a {
  display: block;
  padding-top: 16px;
  padding-bottom: 20px;
  padding-left: 24px;
  padding-right: 24px;
}
.main-navigation li:hover {
  background-color: #293449;
}
.page {
  max-width: 960px;
  margin: 40px auto;
}
.empty-page {
  text-transform: uppercase;
  font-size: 26px;
  width: 960px;
  padding: 5px 20px;
  background: #f3f7f9;
}

