.header-logo:focus {
  outline: none;
  box-shadow: inset 0 -5px 0 rgba(0, 0, 0, 0.24);
  background-color: #ba2732;
}
.scan-input:focus + .scan-icon::before {
  position: absolute;
  content: "";
  width: 17px;
  height: 17px;
  top: 50%;
  left: 17px;
  transform: translateY(-50%);
  opacity: 1;
  background: url("/src/img/icon-search2.png") no-repeat;
}
.scan-input:focus {
  color: black;
  background-color: white;
}
.nav-button:focus {
  outline: none;
  background-color: #161d29;
}
.nav-button__green:focus {
  background-color: #518534;
}
.navigation__button:focus {
  outline: none;
  background: #923137;
}
html {
  background-color: white;
  height: 100%;
}
*,
::after,
::before {
  box-sizing: border-box;
}
@font-face {
  font-family: Cuprum;
  /* Гарнитура шрифта */
  src: url("/fonts/Cuprum-Regular.ttf");
  /* Путь к файлу со шрифтом */
}
body {
  min-width: 960px;
  margin: 0;
  padding: 0;
  font-family: "Cuprum", "Tah oma", sans-serif;
  display: grid;
  align-content: start;
}
button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
li {
  list-style-type: none;
}
li a {
  text-decoration: none;
  color: white;
}

.header-logo:focus {
  outline: none;
  box-shadow: inset 0 -5px 0 rgba(0, 0, 0, 0.24);
